import React from "react";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react";
import { Pagination } from "swiper";

import EasyImg from "../assets/easy.svg";
import DrivenImg from "../assets/driven.svg";
import SecureImg from "../assets/secure.svg";

const Values = ({ className }) => {
  return (
    <section className={className}>
      <div className="content">
        <h4>{"Core values that push us to go further."}</h4>
        <div className="slider-container">
          <Swiper
            modules={[Pagination]}
            spaceBetween={20}
            slidesPerView={1}
            pagination={{ clickable: false }}
            breakpoints={{
              960: {
                slidesPerView: 2,
                spaceBetween: 90,
              },
              1200: {
                slidesPerView: 3,
              },
            }}
          >
            <SwiperSlide>
              <div className="slide-card">
                <div className="image">
                  <img src={EasyImg} alt="Easy" className="slide-image" />
                </div>
                <div className="info">
                  <h5>{"Easy and for Everyone"}</h5>
                  <p className="quote">
                    {
                      "We understand the challenges that engulf a business and how it ultimately slows down the marketing endeavour. Our tools are visually easy and intuitive and are built for a busy individuals, who expects an intelligent assistant to compliment their efforts and drive results."
                    }
                  </p>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slide-card">
                <div className="image">
                  <img src={DrivenImg} alt="Driven" className="slide-image" />
                </div>
                <div className="info">
                  <h5>{"Driven on Innovation"}</h5>
                  <p className="quote">
                    {
                      "Thoughtfulness is the essense of our innovation. We want our customers to achieve more with least effort, our products are built for real world bottleneck situations where it is hard to get around. We love complexities and are proud to innovate industry leading solutions around them."
                    }
                  </p>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="slide-card">
                <div className="image">
                  <img src={SecureImg} alt="Secure" className="slide-image" />
                </div>
                <div className="info">
                  <h5>{"Secure and Reliable"}</h5>
                  <p className="quote">
                    {
                      "Trust is not a mere word for us, it is our commitment to our customers. With our state-of-the-art infrastructure and leading security certification, we endeavour to give our clients a robust and seamless experience. So that your business can run 24x7 with complete peace of mind."
                    }
                  </p>
                </div>
              </div>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default Values;
